<template>
	<view class="main">
		<view class="hand">
			<image src="../../static/tabBarimg/dbleft.png" @click="blackbtn"></image>
			添加充电区
		</view>
		<view class="show">
			<view class="sone">
				<view class="sonefoot">
					<view class="sfshang">
						{{list.projectName}}
					</view>
					<view class="sfxia">
						<view>创建时间：{{list.createTime}}</view>
						<view>项目地址：{{list.address}}</view>
					</view>
				</view>
			</view>
			<view class="stwo">
				<view class="stwohand">
					充电区名称
				</view>
				<input placeholder="请输入充电区的名称" v-model="name" maxlength="50"/>
			</view>
			<view class="stwo">
				<view class="stwohand">
					备注
				</view>
				<textarea placeholder="请输入备注信息" v-model="marks" maxlength="50"/>
			</view>
			<view class="stwo" style="margin-bottom: 20upx;">
				<van-radio-group v-model="radio2" direction="horizontal">
				  <van-radio name="0">充电桩</van-radio>
				  <van-radio name="1">充电柜</van-radio>
				</van-radio-group>
			</view>
			<view class="sthree">
				<view class="sthreehand">
					充电模式
				</view>
				<view class="sthreefoot" >
					<view class="smont">
						<view class="sfleft">
							<view>常用</view> 
							<van-switch v-model="type0" @change="cebtn(0)"/>
						</view>
						<view class="sfright" v-show="type0==true" @click="cebtn(0)">更换</view>
					</view>
					<view class="smontwo" v-show="type0==true&&JSON.stringify(onetap)!='{}'">
						<view class="smleft">
							<view class="smshang">
								{{onetap.packageName}}
							</view>
							<view v-show="onetap.types=='B2'">
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1+'b1'">
									{{item1.standard}}元/度
								</view>
							</view>
							<view v-show="onetap.types=='B5'">
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1+'b1'">
									{{item1.standard}}元/度
								</view>
							</view>
							<view v-show="onetap.types=='B1'">
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1+'a1'"
									v-show="item1.minPower!=item1.maxPower">
									{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元/时</view>
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1+'a2'"
									v-show="item1.minPower==item1.maxPower">{{item1.minPower}}瓦以上-{{item1.standard}}元/时
								</view>
							</view>
							<view v-show="onetap.types=='B3'">
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1+'a1'"
									v-show="item1.minPower!=item1.maxPower">
									{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元/次</view>
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1+'a2'"
									v-show="item1.minPower==item1.maxPower">{{item1.minPower}}瓦以上-{{item1.standard}}元/次
								</view>
							</view>
							<view v-show="onetap.types=='B4'">
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1+'a1'"
									v-show="item1.minPower!=item1.maxPower">
									{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}小时</view>
								<view class="smxia" v-for="(item1, index1) in onetap.steps" :key="index1+'a2'"
									v-show="item1.minPower==item1.maxPower">{{item1.minPower}}瓦以上-{{item1.standard}}小时
								</view>
							</view>
						</view>
						<view class="smright bscolor" v-show="onetap.types=='B1'">计时</view>
						<view class="smright blcolor" v-show="onetap.types=='B2'">计量</view>
						<view class="smright bccolor" v-show="onetap.types=='B3'">计次</view>
						<view class="smright bccolor" style="color:#ffaa00" v-show="onetap.types=='B4'">一元</view>
						<view class="smright bccolor" style="color:red" v-show="onetap.types=='B5'">计量2</view>
					</view>
				</view>
				
				<view class="sthreefoot" v-show="addNew==0">
					<view class="smont">
						<view class="sfleft">
							<view>包月</view> 
							<van-switch v-model="type1" @change="cebtn(1)"/>
						</view>
						<view class="sfright" v-show="type1==true" @click="cebtn(1)">更换</view>
					</view>
					<view class="smontwo" v-show="type1==true&&JSON.stringify(twotap)!='{}'">
						<view class="smleft" >
							<view class="smshang">
								{{twotap.packageName}}
							</view>
								<view class="smxia" v-show="twotap.types=='A1'">{{twotap.monthStandard}}元/月{{twotap.monthNum}}小时，加油包: {{twotap.comeStandard}}元{{twotap.comeNum}}小时</view>
								<view class="smxia" v-show="twotap.types=='A3'">{{twotap.monthStandard}}元/月{{twotap.monthNum}}次，加油包: {{twotap.comeStandard}}元{{twotap.comeNum}}次</view>
						</view>
						<view class="smright bscolor" v-show="twotap.types=='A1'">包时</view>
						<view class="smright bccolor" v-show="twotap.types=='A3'">包次</view>
					</view>
				</view>
				
				
				<view class="sthreefoot" >
					<view class="smont">
						<view class="sfleft">
							<view>临时</view> 
							<van-switch v-model="type2" @change="cebtn(2)"/>
						</view>
						<view class="sfright" v-show="type2==true" @click="cebtn(2)">更换</view>
					</view>
					
					<view class="smontwo" v-show="type2==true&&JSON.stringify(threetap)!='{}'">
						<view class="smleft" >
							<view class="smshang">
								{{threetap.packageName}}
							</view>
								<view class="smxia" v-for="(item1, index1) in threetap.steps" :key="index1+'a3'" v-show="item1.minPower!=item1.maxPower">{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元/时</view>
								<view class="smxia" v-for="(item1, index1) in threetap.steps" :key="index1+'a4'" v-show="item1.minPower==item1.maxPower">{{item1.minPower}}瓦以上-{{item1.standard}}元/时</view>
						</view>
						<view class="smright bscolor" v-show="threetap.types=='C1'">计时</view>
					</view>
					
				</view>
			</view>
			<view class="sfour" @click="addcd">
				立即添加
			</view>
			<van-popup v-model="wshow" :close-on-click-overlay="false">
				<view class="tannei1">
					<view class="tnhand">
						<view class="tntxt">请选择一个费率</view>
						<image src="../../static/tabBarimg/close.png" @click="wshowclose"></image>
					</view>
					<view class="tnfoot">
							  <view class="montlist">
								  <van-radio-group v-model="radio1" @change='changebtn'>
									  <view class="mont" v-for="(item, index) in alist" :key="index+'7'"  v-show="active==0&&alist.length>0">
										  <view class="montleft">
												  <view v-show="item.types=='B2'">
												  	<view class="montshang">{{item.packageName}}</view>
												  	<view class="montxia">{{item.steps[0].standard}}元/度</view>
												  </view>
												  <view v-show="item.types=='B5'">
												  	<view class="montshang">{{item.packageName}}</view>
												  	<view class="montxia">{{item.steps[0].standard}}元/度</view>
												  </view>
												  <view v-show="item.types=='B1'">
												  	<view class="montshang">{{item.packageName}}</view>
												  	<view class="montxia" v-for="(item1, index1) in item.steps"
												  		:key="index1+'1'" v-show="item1.minPower!=item1.maxPower">
												  		{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元/时</view>
												  	<view class="montxia" v-for="(item1, index1) in item.steps"
												  		:key="index1+'2'" v-show="item1.minPower==item1.maxPower">
												  		{{item1.minPower}}瓦以上-{{item1.standard}}元/时</view>
												  </view>
												  <view v-show="item.types=='B3'">
												  	<view class="montshang">{{item.packageName}}</view>
												  	<view class="montxia" v-for="(item1, index1) in item.steps"
												  		:key="index1+'1'" v-show="item1.minPower!=item1.maxPower">
												  		{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元/次</view>
												  	<view class="montxia" v-for="(item1, index1) in item.steps"
												  		:key="index1+'2'" v-show="item1.minPower==item1.maxPower">
												  		{{item1.minPower}}瓦以上-{{item1.standard}}元/次</view>
												  </view>
												  <view v-show="item.types=='B4'">
												  	<view class="montshang">{{item.packageName}}</view>
												  	<view class="montxia" v-for="(item1, index1) in item.steps"
												  		:key="index1+'1'" v-show="item1.minPower!=item1.maxPower">
												  		{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}小时</view>
												  	<view class="montxia" v-for="(item1, index1) in item.steps"
												  		:key="index1+'2'" v-show="item1.minPower==item1.maxPower">
												  		{{item1.minPower}}瓦以上-{{item1.standard}}小时</view>
												  </view>
										  </view>
										  <view class="montright">
											  <view class="mrtxt bscolor" v-show="item.types=='B1'">计时</view>
											  <view class="mrtxt blcolor" v-show="item.types=='B2'">计量</view>
											  <view class="mrtxt bccolor" v-show="item.types=='B3'">计次</view>
											  <view class="mrtxt bccolor" style="color:#ffaa00" v-show="item.types=='B4'">一元</view>
											   <view class="mrtxt bccolor" style="color:red" v-show="item.types=='B5'">计量2</view>
											  <van-radio :name="item.packageId"/>
										  </view>
									  </view>
									  <view v-show="active==0&&alist.length==0" style="margin: 120upx 50upx; color: #007AFF;" @click="gopz">
									  	您目前还未配置费率，点此马上配置；
									  </view>
									  <view class="mont" v-for="(item, index) in clist" :key="index"  v-show="active==2&&clist.length>0">
										  <view class="montleft">
											  <view class="montshang">{{item.packageName}}</view>
											  <view class="montxia" v-for="(item1, index1) in item.steps" :key="index1+'3'" v-show="item1.minPower!=item1.maxPower">{{item1.minPower}}-{{item1.maxPower}}瓦-{{item1.standard}}元/时</view>
											  <view class="montxia" v-for="(item1, index1) in item.steps" :key="index1+'4'" v-show="item1.minPower==item1.maxPower">{{item1.minPower}}瓦以上-{{item1.standard}}元/时</view>
										  </view>
										  <view class="montright">
											  <view class="mrtxt bscolor" v-show="item.types=='C1'">计时</view>
											  <van-radio :name="item.packageId"/>
										  </view>
									  </view>
									  <view v-show="active==2&&clist.length==0" style="margin: 120upx 50upx; color: #007AFF;" @click="gopz">
									  	您目前还未配置费率，点此马上配置；
									  </view>
									  <view class="mont" v-for="(item, index) in blist" :key="index+'5'"  v-show="active==1&&blist.length>0">
										  <view class="montleft">
											  
												  <view class="montshang">{{item.packageName}}</view>
												  <view class="montxia" v-show="item.types=='A1'">{{item.monthStandard}}元/月{{item.monthNum}}小时，加油包: {{item.comeStandard}}元{{item.comeNum}}小时</view>
												  <view class="montxia" v-show="item.types=='A3'">{{item.monthStandard}}元/月{{item.monthNum}}次，加油包: {{item.comeStandard}}元{{item.comeNum}}次</view>
											  
										  </view>
										  <view class="montright">
											  <view class="mrtxt bscolor" v-show="item.types=='A1'">包时</view>
											  <view class="mrtxt bccolor" v-show="item.types=='A3'">包次</view>
											  
											  <van-radio :name="item.packageId"/>
										  </view>
									  </view>
									  <view v-show="active==1&&blist.length==0" style="margin: 120upx 50upx; color: #007AFF;" @click="gopz">
									  	您目前还未配置费率，点此马上配置；
									  </view>
								  </van-radio-group>
							  </view>
					</view>
				</view>
			</van-popup>
			
		</view>
	</view>
</template>

<script>
	
	export default {
		components: {},
		mounted() {
			
		},

		data() {
			return {
				showlist:[{name:'常用',type:false},{name:'包月',type:false},{name:'临时',type:false}],
				// checked: false,
				radio2:'0',
				wshow:false,
				active: 0,
				radio1: '',
				projectId:'',
				list:{},//小区详情
				alist:[],//常用费率列表
				blist:[],//包月费率列表
				clist:[],//临时费率列表
				onetap:{},//常用费率数据
				twotap:{},//包月费率数据
				threetap:{},//临时费率数据
				commonId:'',//常用费率套餐id
				monthId:'',//包月费率套餐id
				tmpId:'',//临时费率套餐id
				type0:false,
				type1:false,
				type2:false,
				name:'',//充电区名
				marks:'',//备注信息
				addNew:0,
				ctype:'',//30天包月套餐类型

			}
		},
		onLoad(options) {
			this.projectId=options.projectId
			this.addNew=options.addNew
		},
		onShow() {
			this.main()
			this.wshowclose()
			this.show()
		},
		methods: {
			show(){
				let data={
					type:1
				}
				this.$base.request1('manager/rate/list'+'/'+this.projectId, 'GET',data)
				.then(res => {
					if(res.data.code=='200'){
						this.ctype=res.data.data.rows[0].types
					}else{
						this.$toast.fail(res.data.msg)
					}
				})
				.catch(err => {
					
				})
			},
			//获取某一小区详情和总用户数等信息
			main(){
				let data={
					projectId:this.projectId,
					needProjectMoney:0,
					needProportion:0
				}
				this.$base.request1('manager/project/statistics/', 'GET',data)
					.then(res => {
						this.list=res.data.data
					})
					.catch(err => {
				
					})
			},
			//返回上一页
			blackbtn() {
				uni.navigateBack()
			},
			cebtn(vul){
				this.active=vul
				if(vul==0){
					if(this.type0==true){
						this.wshowopen();
					}
				}else if(vul==1){
					if(this.type1==true){
						this.wshowopen();
					}
				}else if(vul==2){
					if(this.type2==true){
						this.wshowopen();
					}
				}
				
			},
			//关闭弹窗
			wshowclose() {
				this.radio1=''
				if(JSON.stringify(this.onetap)=='{}'){
					this.type0 = false
				}
				if(JSON.stringify(this.twotap)=='{}'){
					this.type1 = false
				}
				if(JSON.stringify(this.threetap)=='{}'){
					this.type2 = false
				}
				this.wshow = false
			},
			//打开弹窗
			wshowopen() {
				this.wshow = true
				this.radio1=''
				let data={
					type:this.active
				}
				this.$base.request1('manager/rate/list'+'/'+this.projectId, 'GET',data)
				.then(res => {
					if(this.active==0){
						this.alist=res.data.data.rows
					}else if(this.active==1){
						this.blist=res.data.data.rows
					}else if(this.active==2){
						this.clist=res.data.data.rows
					}
				})
				.catch(err => {
					
				})
			},
			changebtn(){
				
				this.$base.request1('manager/rate'+'/'+this.radio1, 'GET')
				.then(res => {
					if(this.active==0){
						this.onetap=res.data.data[0]
						this.commonId=this.radio1
					}else if(this.active==1){
						this.twotap=res.data.data[0]
						this.monthId=this.radio1
					}else if(this.active==2){
						this.threetap=res.data.data[0]
						this.tmpId=this.radio1
					}
					this.wshow = false
				})
				.catch(err => {
					
				})
			},
			//添加充电区
			addcd(){
				if (this.type0 == false) {
					this.commonId = ''
					this.onetap = {}
				}
				if (this.type1 == false) {
					this.monthId = ''
					this.twotap = {}
				}
				if (this.type2 == false) {
					this.tmpId = ''
					this.threetap = {}
				}
				if(!this.name){
					this.$toast.fail('充电区名称不能为空')
					return;
				}
				if(this.radio2=='1'){
					if(this.ctype&&this.ctype!='A4'){
						this.$toast.fail('当前小区包月费率不允许添加包含充电柜的充电区')
						return;
					}
					
				}
				let data={
					commonId:this.commonId,
					monthId:this.monthId,
					tmpId:this.tmpId,
					areaName:this.name,
					marks:this.marks,
					areaType:this.radio2
				}
				// let header={
				// 	'content-type': 'application/json'
				// }
				this.$base.request1('manager/area'+'/'+this.projectId, 'POST',data)
				.then(res => {
					if(res.data.code=='200'){
						uni.setStorageSync('typetwo',1)
						this.$toast.success('添加成功')
						uni.navigateBack()
					}else{
						this.$toast.fail(res.data.msg)
					}
				})
				.catch(err => {
					
				})
			},
			//跳转配置费率页面
			gopz(){
				uni.navigateTo({
					url: '../rate_setting/rate_setting?projectId='+this.projectId,
				});
			},
		}
	}
</script>

<style lang="scss" scoped>
	.main {
		width: 100%;
		overflow-x: hidden;
		.hand{
			width: 100%;
			background-color: #4B98ED;
			height: 62upx;
			font-size: 30upx;
			font-family: PingFang SC Bold, PingFang SC Bold-Bold;
			font-weight: 700;
			text-align: center;
			line-height: 62upx;
			color: #ffffff;
			position: relative;
			image{
				width:22upx;
				height: 39upx;
				position: absolute;
				left: 31upx;
				top: 11upx;
			}
		}
		
		/deep/.van-radio__label{
			font-family: PingFang SC Bold, PingFang SC Bold-Bold;
			font-size: 28upx;
			font-weight: 700;
			color: #333333;
			margin-right: 30upx;
		}
		.show{
			width: 100%;
			box-sizing: border-box;
			//包时字体颜色
			.bscolor{
				color: #F041CC;
			}
			//包次字体颜色
			.bccolor{
				color: #4B98ED;
			}
			//包量字体颜色
			.blcolor{
				color: #4BED79;
			}
			.sone{
				width: 100%;
				height: 250upx;
				background-color: #4b98ED;
				margin-bottom: 38upx;
				position: relative;
				.sonefoot{
					position: absolute;
					width: 686upx;
					height: 200upx;
					border-radius: 8upx;
					box-shadow: 0upx 2upx 9upx 0upx rgba(22,22,22,0.1); 
					background-color: #FFFFFF;
					left: 32upx;
					top:81upx;
					box-sizing: border-box;
					padding: 34upx 21upx;
					.sfshang{
						font-size: 32upx;
						font-family: PingFang SC Bold, PingFang SC Bold-Bold;
						font-weight: 700;
						color: #333333;
						width: 500upx;
						overflow: hidden;
						text-overflow:ellipsis;
						white-space: nowrap;
					}
					.sfxia{
						margin-top: 30upx;
						font-size: 28upx;
						font-family: PingFang SC Medium, PingFang SC Medium-Medium;
						font-weight: 500;
						color: #333333;
						line-height: 41upx;
					}
				}
			}
			.stwo{
				width: 100%;
				box-sizing: border-box;
				padding: 28upx 32upx;
				/deep/.van-radio-group{
					display: flex;
				}
				.stwohand{
					font-size: 28upx;
					font-family: PingFang SC Bold, PingFang SC Bold-Bold;
					font-weight: 700;
					color: #333333;
				}
				input{
					width: 100%;
					height: 60upx;
					font-size: 28upx;
